@keyframes scaling-dots {
  0%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40%, 60% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.start-page__inputblock-loader {
  display: none;
  transition: 0.3s display ease;
  height: 10px;
  grid-gap: 4.5px;
  grid-template-columns: repeat(5, 10px);
}

