[class\$=__container] {
  max-width: 1342px;
  margin: 0 auto;
  padding: 0 12px;
}

.wrapper {
  font-size: 20px;
  font-weight: 400;
  font-family: Nunito;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.active {
  background-color: #BFBFD4 !important;
  color: #3B3B54;
}

.fadeIn {
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.header {
  transition: 0.5s ease;
}
.header__container {
  display: flex;
  align-items: center;
  margin-top: 48px;
}
.header__text {
  color: #BFBFD4;
}

.page {
  margin-top: 160px;
}

.start-page {
  transition: 0.5s ease;
}
.start-page__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.start-page__textblock {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 56px;
}
.start-page__textblock-title {
  margin-bottom: 8px;
  font-size: 32px;
  font-weight: 700;
}
.start-page__textblock-text {
  color: #BFBFD4;
}
.start-page__textblock span {
  color: #8FB2F5;
}
.start-page__inputblock {
  position: relative;
  width: 100%;
}
.start-page__inputblock-loader {
  position: absolute;
  top: 22px;
  right: -12px;
}
.start-page__citiesblock {
  margin-top: 8px;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
.start-page__citiesblock-city {
  box-sizing: border-box;
  transition: 0.5s ease;
  display: none;
  position: absolute;
  top: 49px;
  opacity: 0;
  width: 100%;
  background-color: #3B3B54;
  padding: 16px 20px;
  font-size: 16px;
  border-bottom: 1px solid #1E1E29;
}
.start-page__citiesblock-city:first-child {
  top: 0;
  border-radius: 8px 8px 0 0;
}
.start-page__citiesblock-city:last-child {
  top: 98px;
  border-radius: 0 0 8px 8px;
  border-bottom: none;
}
.start-page__notfound {
  display: none;
  flex-direction: column;
  align-items: center;
  margin-top: 36px;
}
.start-page__notfound-img {
  width: 80px;
}
.start-page__notfound-text {
  font-size: 24px;
  margin-top: 9px;
  font-weight: 600;
  color: #7F7F98;
}

@media (max-width: 768px) {
  .header__text {
    font-size: 26px;
  }
  .start-page__textblock-title {
    font-size: 20px;
  }
  .start-page__textblock-text {
    font-size: 14px;
  }
}
#searchcity {
  width: 100%;
  background-color: #1E1E29;
  padding: 17px 20px;
  border-radius: 8px;
  border: none;
  box-sizing: border-box;
}

#searchcity:focus {
  outline: none;
}

#searchcity::placeholder {
  color: #7F7F98;
  font-size: 16px;
}

#searchcity:not(:placeholder-shown) {
  font-size: 16px;
  color: #FAFAFA;
}

#weather-page__logo {
  cursor: pointer;
}

.weather-page {
  display: none;
  transition: 0.5s opacity ease;
}
.weather-page__container {
  display: flex;
}
.weather-page__previewblock {
  padding: 16px;
  background-color: #16161F;
  border-radius: 12px;
}
.weather-page__previewblock-search {
  display: flex;
  align-items: center;
}
.weather-page__previewblock-info {
  margin-top: 16px;
  position: relative;
  z-index: 10;
}
.weather-page__previewblock-info-bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 616px;
  border-radius: 8px;
}
.weather-page__previewblock-info-decor {
  position: absolute;
  bottom: -40px;
  right: -20px;
  z-index: -1;
  width: 40%;
}
.weather-page__logo-img {
  padding: 4.5px;
  margin-right: 12px;
  background-color: #1E1E29;
  border-radius: 8px;
}
.weather-page__inputblock {
  position: relative;
  width: 100%;
}
.weather-page__inputblock-loader {
  position: absolute;
  top: 22px;
  right: -12px;
}
.weather-page__topblock {
  padding: 32px 32px 0 32px;
  display: flex;
  justify-content: space-between;
}
.weather-page__topblock-titleblock {
  margin-right: 260px;
}
.weather-page__topblock-titleblock-title {
  font-weight: 700;
  margin-bottom: 8px;
  white-space: nowrap;
}
.weather-page__topblock-titleblock-date {
  font-size: 16px;
}
.weather-page__topblock-time {
  font-weight: 700;
}
.weather-page__botblock {
  padding: 4px;
  margin: 350px 0 36px 32px;
}
.weather-page__botblock-degrees {
  font-weight: 900;
  font-size: 96px;
  margin-bottom: 12px;
}
.weather-page__botblock-description {
  display: flex;
  align-items: center;
  gap: 12px;
  line-height: 140%;
}
.weather-page__botblock-avgdegrees {
  font-weight: 700;
}
.weather-page__botblock-decorimg {
  width: 8px;
  height: 8px;
  background-color: hsla(0, 0%, 100%, 0.4);
  border-radius: 4px;
}
.weather-page__infoblocks {
  display: flex;
  flex-direction: column;
  margin-left: 40px;
}
.weather-page__infoblocks-today {
  padding: 28px 24px 8px 24px;
  background-color: #16161F;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.weather-page__infoblocks-today-title {
  font-size: 16px;
  color: #7F7F98;
  margin-bottom: 36px;
  padding-right: 410px;
  line-height: 140%;
}
.weather-page__infoblocks-nextdays {
  background-color: #16161F;
  display: flex;
  flex-direction: column;
  padding: 28px 24px 24px 24px;
  border-radius: 12px;
  margin-top: 16px;
}
.weather-page__infoblocks-nextdays-title {
  font-size: 16px;
  color: #7F7F98;
  line-height: 140%;
  margin-bottom: 20px;
}
.weather-page__details {
  display: flex;
  align-items: center;
  position: relative;
  margin: 16px 0;
}
.weather-page__details-img {
  max-width: 32px;
  height: auto;
  margin-right: 12px;
  float: left;
  position: relative;
}
.weather-page__details-text {
  position: relative;
  float: left;
  color: #BFBFD4;
  font-size: 14px;
  font-weight: 700;
}
.weather-page__details [class^=weather-page__details-value] {
  position: absolute;
  right: 0;
  float: right;
}
.weather-page__forecast {
  display: flex;
  align-items: center;
}
.weather-page__block {
  padding: 28.5px 24.7px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.weather-page__block-title, .weather-page__block-text, .weather-page__block-degrees {
  color: #BFBFD4;
  font-size: 14px;
  font-weight: 700;
}
.weather-page__block-img {
  max-width: 67px;
  height: auto;
  margin: 12px 0;
}
.weather-page__block-degrees {
  margin-top: 4px;
  color: #FAFAFA;
}

@media (max-width: 768px) {
  body {
    overflow: scroll;
  }
  .weather-page {
    margin-bottom: 25px;
  }
  .weather-page__container {
    flex-direction: column;
  }
  .weather-page__previewblock-info-bg {
    height: 304px;
  }
  .weather-page__previewblock-info-decor {
    right: 0;
    bottom: -5px;
  }
  .weather-page__topblock {
    padding: 20px 20px 0 20px;
  }
  .weather-page__topblock-time {
    font-size: 12px;
  }
  .weather-page__topblock-titleblock {
    margin-right: 0;
  }
  .weather-page__topblock-titleblock-title {
    font-size: 16px;
  }
  .weather-page__topblock-titleblock-date {
    font-size: 12px;
  }
  .weather-page__botblock {
    margin: 144px 0 0 16px;
  }
  .weather-page__botblock-degrees {
    font-size: 48px;
  }
  .weather-page__botblock-avgdegrees {
    font-size: 16px;
  }
  .weather-page__botblock-decorimg {
    display: none;
  }
  .weather-page__botblock-text {
    font-size: 14px;
  }
  .weather-page__infoblocks {
    margin-left: 0px;
  }
  .weather-page__infoblocks-today-title {
    display: none;
  }
  .weather-page__infoblocks-nextdays-title {
    display: none;
  }
  .weather-page__details-img {
    max-width: 24px;
  }
  .weather-page__details [class^=weather-page__details-value] {
    font-size: 16px;
  }
  .weather-page__block {
    padding: 0;
  }
  .weather-page__block-text {
    display: none;
  }
  .weather-page__block-img {
    max-width: 56px;
    margin: 4px 0;
  }
  .weather-page__forecast {
    justify-content: space-between;
  }
